:root {
  --primary: #fff;
}

.btn {
  padding: 8px 20px;
  border-radius: 3px;
  outline: none;
  border: none;
  cursor: pointer;
  margin-left: -35px;
}

.btn--primary {
  background-color: var(--primary);
  color: #000000;
  border: 1px solid var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: #fff;
  padding: 0px;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
}

.btn--medium {
  padding: 2px 8px;
  font-size: 28px;
}

.btn--large {
  padding: 5px 15px;
  font-size: 30px;
}

.btn--large:hover,
.btn--medium:hover {
  transition: all 0.3s ease-out;
  background: #fff;
  color: #000000;
  transition: 250ms;
}